<template>
  <div class="detail">
    <div
      class="detail-header"
      :style="{ background: `rgba(0, 0, 0,${opacityState})` }"
    >
      <div class="header-left" @click="backPrePage">
        <span class="iconfont icon-arrow-left-bold"></span>
      </div>
      <div
        class="header-center"
        :style="{ color: `rgba(255, 255, 255,${opacityState})` }"
      >
        {{ detailDataObj?.title }}
      </div>
      <div class="header-right">
        <div class="header-right-loading">
          <span class="iconfont icon-icondownload"></span>
        </div>
        <div class="header-right-share">
          <span class="iconfont icon-fenxiang1"></span>
        </div>
      </div>
    </div>
    <div
      class="detail-body"
      v-if="Object.keys(detailDataObj).length != 0"
      @scroll.stop="scrollEvent($event)"
    >
      <div class="body-container">
        <div
          class="body-image"
          :style="{ backgroundImage: `url(${detailDataObj.horizontal_cover})` }"
        >
          <div
            class="chase-button"
            :class="{ 'chase-finished': isShowChase }"
            @click="ChaseStateEvent"
          >
            <span class="iconfont icon-shoucang1"></span>
            <span class="i-c">追漫</span>
          </div>
        </div>
        <div class="body-main">
          <div class="main-title">{{ detailDataObj.title }}</div>
          <div class="main-description">
            <div class="description-first" v-if="detailDataObj.hall_icon_text">
              <div class="hall-icon-text">
                {{ detailDataObj.hall_icon_text }}
              </div>
            </div>
            <div class="description-works">
              <div class="works-author">
                {{ detailDataObj?.author_name[0] || "未知" }}
              </div>
              <div class="works-style">
                <div
                  class="works-style-item"
                  v-for="it in detailDataObj.styles"
                  :key="it"
                >
                  {{ it }}
                </div>
              </div>
            </div>
            <div class="description-state">
              <div class="description-state-left">
                <span>{{
                  detailDataObj.is_finish == 1 ? "已完结" : "未完结"
                }}</span>
                · <span>共{{ detailDataObj.total || "0" }}话</span>
              </div>
              <div class="description-state-right" @click="showHideEvent">
                <span
                  class="iconfont icon-xiangxia"
                  v-show="!isShowContext"
                ></span
                ><span
                  class="iconfont icon-xiangshang"
                  v-show="isShowContext"
                ></span>
              </div>
            </div>
            <div class="description-detail" v-show="isShowContext">
              <div class="detail-a">
                <div class="a-text">作者:</div>
                <div class="a-name">
                  {{ detailDataObj.author_name.join(" 、") || "未知" }}
                </div>
              </div>
              <div class="detail-b">
                <div class="b-brief">简介:</div>
                <div class="b-text">
                  {{ detailDataObj.classic_lines || "描述为空" }}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="body-series" v-if="isSeriesEmpty">
          <div class="series-title">系列作品</div>
          <div class="series-works" v-show="!isShowSeriesLoad">
            <div
              class="works-items"
              v-for="it in seriesListData"
              :key="it.id"
              @click="getMoreData(it.id, it)"
            >
              <div
                class="works-items-img"
                :style="{ backgroundImage: `url(${it.horizontal_cover})` }"
              ></div>
              <div class="works-items-text">{{ it.title }}</div>
            </div>
          </div>
          <div class="loading-icon" v-show="isShowSeriesLoad">
            <van-loading type="spinner" color="#1989fa" size="20px" />
          </div>
        </div>
        <div class="body-recommend" v-if="moreRecommendData.length != 0">
          <div class="recommend-header">
            <div class="recommend-header-title">更多推荐</div>
            <div class="recommend-header-updata" @click="UpatePartData">
              <span class="iconfont icon-gengxin" ref="refreshRotate"></span>
              <span>换一换</span>
            </div>
          </div>
          <div class="recommend-content" v-show="!isShowMoreRecommend">
            <div
              class="content-item"
              v-for="items in renderRecommendPart"
              :key="items.id"
              @click="ShowPartListEvent(items.cartoon_text, items.id, items)"
            >
              <div
                class="content-item-img"
                :style="{ backgroundImage: `url(${items.horizontal_cover})` }"
                :class="{ 'content-item-ig': items.cartoon_text == 'part' }"
              >
                <div class="img-count" v-if="items.cartoon_text == 'part'">
                  {{ authorRecommendData.length }}部
                </div>
              </div>
              <div class="content-item-title">{{ items.title }}</div>
              <div class="content-item-updata" v-if="!items.cartoon_text">
                {{
                  items.is_finish == 0
                    ? `更新至${items.last_ord}话`
                    : `[完结]共${items.total}话`
                }}
              </div>
              <div class="content-item-a" v-if="items.cartoon_text">
                <div class="a-author">同作者作品</div>
              </div>
            </div>
          </div>

          <div class="loading-icon" v-show="isShowMoreRecommend">
            <van-loading type="spinner" color="#1989fa" size="20px" />
          </div>
        </div>
        <div class="load-icon" v-if="moreRecommendData.length == 0">
          <van-loading type="spinner" color="#1989fa" />
        </div>
      </div>
    </div>
    <div class="detail-footer" v-if="Object.keys(detailDataObj).length != 0">
      <div class="footer-left">
        <div class="footer-left-icon">
          <span class="iconfont icon-mulu"></span>
        </div>
        <div class="footer-left-text" @click.stop="closeCurrentList">目录</div>
      </div>
      <div class="footer-center">
        <div class="footer-list">
          <div
            class="footer-item"
            v-for="item in renderListArr"
            :key="item.id"
            :class="{ 'f-active': item.id == activeId }"
            @click="selectCartoonEvent(item, item.title)"
          >
            {{ item.ord }}
          </div>
        </div>
      </div>
      <div class="footer-right" @click="getImageIndexEvent(activeId)">续看</div>
    </div>
    <transition name="move">
      <div class="detail-catalog" v-if="isShowCatalog">
        <div class="catalog-container">
          <div class="catalog-container-header" @click.stop="closeCurrentList">
            <span class="iconfont icon-close"></span>
          </div>
          <div class="catalog-container-body">
            <div class="container-body-title">
              <div class="title-l">全部章节({{ renderListArr.length }})</div>
            </div>
            <div class="container-body-list">
              <div
                class="c-body-item"
                v-for="its in renderListArr"
                :key="its.id"
                @click="getImageIndexEvent(its.id, its.title)"
              >
                <!-- <div
                  class="b-item-img"
                  :style="{ backgroundImage: `url(${its.cover})` }"
                ></div> -->

                <div class="b-item-img">
                  <van-image class="b-i-ig" :src="its.cover"></van-image>
                </div>

                <div class="b-item-area">
                  <div class="area-title">
                    {{ its.title }}
                  </div>
                  <div class="area-other">
                    <div class="area-time">
                      {{ its.pub_time.split(" ")[0] }}
                    </div>
                    <div class="area-commend">
                      <span class="iconfont icon-pinglun"></span
                      ><span>{{ its.comments || 0 }}</span>
                    </div>
                    <div class="thumbs-up">
                      <span class="iconfont icon-recommend"></span
                      ><span>{{ its.like_count || 0 }}</span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="empty-text-des">到底啦~~</div>
            </div>
          </div>
        </div>
        <div class="circle-r" @click="resetRankEvent">
          <div class="t-r-icon">
            <span class="iconfont icon-shengjiang"></span>
          </div>
          <div class="t-r-text">{{ liftingState }}</div>
        </div>
      </div>
    </transition>

    <div
      class="detail-other"
      v-if="isShowOther"
      @click.self="closeCurrentLayer"
    >
      <transition name="move">
        <div class="other-container">
          <div class="container-header">
            <div class="other-container-left" @click.stop="closeCurrentLayer">
              <span class="iconfont icon-close"></span>
            </div>
            <div class="other-container-title">
              {{ detailDataObj?.author_name[0] || "" }}其他作品
            </div>
          </div>

          <div class="container-body">
            <div class="other-list">
              <div
                class="other-list-item"
                v-for="i in authorRecommendData"
                :key="i.id"
                @click="getMoreData(i.id)"
              >
                <div
                  class="list-item-img"
                  :style="{ backgroundImage: `url(${i.horizontal_cover})` }"
                ></div>
                <div class="list-item-content">
                  <div class="item-content-title">
                    {{ i.title }}
                  </div>
                  <div class="item-content-des">
                    <div class="content-des-author">
                      <span
                        class="d-a"
                        v-for="(a, ind) in i.authors"
                        :key="a.id + ind"
                        >{{ a.cname }}</span
                      >
                    </div>
                    <div class="content-des-main">
                      <div class="des-main-left">
                        <div class="main-left-style">
                          <span class="l-s" v-for="b in i.styles" :key="b.id">{{
                            b.name
                          }}</span>
                        </div>
                        <div class="main-left-finished">
                          {{
                            i.is_finish == 1
                              ? "已完成"
                              : `更新至${i.last_ord}话`
                          }}
                        </div>
                      </div>
                      <div class="des-main-right">
                        <div class="des-main-button">
                          <span class="iconfont icon-shoucang"> </span>
                          <span>追漫</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="other-tips">没有更多啦~~</div>
          </div>
        </div>
      </transition>
    </div>

    <div class="empty-loading" v-if="Object.keys(detailDataObj).length == 0">
      <van-loading type="spinner" color="#1989fa" />
    </div>

    <ArticlePreviewView
      v-if="isCloseArticle"
      :articleData="articleData"
      @closeArticlePage="closeArticlePage"
      :isShowArticle="isShowArticle"
      :ArticleTitle="ArticleTitle"
    />
  </div>
</template>
<style lang="scss" scoped>
