    padding-top: 50%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 668;
    .other-container {
      width: 100%;
      height: 100%;
      background: rgb(16, 16, 16);
      box-shadow: 0px 0px 6px gray;
      .container-header {
        display: flex;
        align-items: center;
        height: 40px;
        .other-container-left {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 30px;
          height: 40px;
          .icon-close {
            font-size: 19px;
          }
        }
        .other-container-title {
          width: calc(100% - 30px);
          height: 40px;
          line-height: 40px;
          text-align: center;
          padding-right: 15px;
          font-size: 15px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
      .container-body {
        width: 100%;
        height: calc(100% - 40px);
        overflow: scroll;
        &::-webkit-scrollbar {
          width: 0px;
          height: 0px;
        }
        .other-list {
          width: 100%;
          .other-list-item {
            display: flex;
            padding: 10px;
            height: 130px;

            .list-item-img {
              width: 100px;
              height: 100%;
              margin-right: 10px;
              background-size: cover;
              background-position: center center;
              background-repeat: no-repeat;
            }
            .list-item-content {
              width: calc(100% - 110px);
              display: flex;
              flex-direction: column;
              justify-content: space-between;
              .item-content-title {
                width: 100%;
                height: 30px;
                line-height: 30px;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
                font-size: 14px;
              }
              .item-content-des {
                width: 100%;
                color: #ccc;
                font-size: 13px;
                .content-des-author {
                  width: 100%;
                  height: 20px;
                  line-height: 20px;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  white-space: nowrap;

                  .d-a {
                    margin-right: 3px;
                  }
                }
                .content-des-main {
                  display: flex;
                  justify-content: space-between;
                  align-items: center;

                  .main-left-style,
                  .main-left-finished {
                    height: 20px;
                    line-height: 20px;
                  }
                  .l-s {
                    margin-right: 3px;
                  }

                  .des-main-right {
                    .des-main-button {
                      display: flex;
                      align-items: center;
                      justify-content: center;
                      padding: 3px 10px;
                      border: 1px solid gray;
                      border-radius: 999px;

                      .icon-shoucang {
                        margin-right: 3px;
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .other-tips {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 60px;
          font-size: 13px;
          color: #ccc;
        }
      }
    }
  }

  .empty-loading {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
  }

  .refreshIt {
    animation: animat 0.3s linear infinite;
  }

  @keyframes animat {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(180deg);
    }
  }
}

.load-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 60px;
}

.move-enter,
.move-leave-to {
  transform: translateY(100%);
}
.move-enter-active,
.move-leave-active {
  transition: all 0.2s linear;
}
.move-enter-to,
.move-leave {
  transform: translateY(0px);
}
</style>
<script>
import {
  detailData,
  detailSeriesList,
  detailMoreRecommend,
  detailGetImageIndex,
  detailGetImageToken,
} from "../../api/index";
import ArticlePreviewView from "../../components/ArticlePreviewView.vue";
import { mapState } from "vuex";
export default {
  data() {
    return {
      Did: "",
      isShowContext: false,
      detailDataObj: {},
      seriesListData: [],
      isShowSeriesLoad: false,
      isSeriesEmpty: false,
      moreRecommendData: [],
      renderRecommendPart: [],
      authorRecommendData: [],
      isShowMoreRecommend: false,
      getCount: 0,
      timer: null,
      opacityState: 0,
      isShowOther: false,
      activeId: "",
      renderListArr: [],
      liftingState: "升序",
      isShowCatalog: false,
      isShowChase: false,
      isCloseArticle: false,
      articleData: [],
      isShowArticle: false,
      ArticleTitle: "",
    };
  },
  created() {
    this.Did = this.$route.params.id;
    this.getDetailData();
    this.initChaseStorage();
  },
  methods: {
    showHideEvent() {
      this.isShowContext = !this.isShowContext;
    },
    backPrePage() {
      this.$router.go(-1);
    },
    async getDetailData() {
      if (this.Did == "") {
        return;
      }
      this.detailDataObj = await detailData(this.Did);
      this.renderListArr = this.detailDataObj.ep_list;
      if (this.renderListArr.length != 0) {
        this.renderListArr.sort(function (a, b) {
          return a.ord - b.ord;
        });
        this.activeId = this.renderListArr[0].id;
      }
      this.getDetailSeriesData();
      this.getMoreRecommendData();
    },
    async getDetailSeriesData() {
      this.isShowSeriesLoad = true;
      this.seriesListData = await detailSeriesList(this.Did);
      this.isShowSeriesLoad = false;
      if (this.seriesListData.length != 0) {
        this.isSeriesEmpty = true;
      } else {
        this.isSeriesEmpty = false;
      }
    },
    async getMoreRecommendData() {
      this.isShowMoreRecommend = true;
      let data = await detailMoreRecommend(this.Did);
      if (data.author_comics.length != 0) {
        this.moreRecommendData = [
          {
            ...data.author_comics[0],
            cartoon_text: "part",
            cartoon_count: data.author_comics.length,
          },
          ...data.recommend_comics,
        ];
        this.authorRecommendData = data.author_comics;
      } else {
        this.moreRecommendData = data.recommend_comics;
      }
      this.isShowMoreRecommend = false;
      this.getUpateData(this.getCount);
    },
    getUpateData(num) {
      this.renderRecommendPart = [];
      for (let i = 0; i < 6; i++) {
        this.renderRecommendPart = [
          ...this.renderRecommendPart,
          this.moreRecommendData[i + num],
        ];
      }
    },
    UpatePartData() {
      if (this.timer) {
        clearTimeout(this.timer);
      }
      let self = this.$refs.refreshRotate;
      self.classList.add("refreshIt");
      this.timer = setTimeout(() => {
        self.classList.remove("refreshIt");
      }, 300);

      if (this.getCount + 6 > this.moreRecommendData.length - 6) {
        this.getCount = 0;
      } else {
        this.getCount = this.getCount + 6;
      }
      this.getUpateData(this.getCount);
    },
    scrollEvent(e) {
      if (e.target.scrollTop < 250) {
        this.opacityState = (e.target.scrollTop / 240).toFixed(1);
      } else {
        this.opacityState = 1;
      }
    },
    closeCurrentLayer() {
      this.isShowOther = false;
    },
    ShowPartListEvent(text, id, obj) {
      if (text == "part") {
        this.isShowOther = true;
      } else {
        this.getMoreData(id, obj);
      }
    },
    getMoreData(id, obj) {
      this.isShowContext = false;
      this.detailDataObj = {};
      this.seriesListData = [];
      this.isShowSeriesLoad = false;
      this.isSeriesEmpty = false;
      this.moreRecommendData = [];
      this.renderRecommendPart = [];
      this.authorRecommendData = [];
      this.isShowMoreRecommend = false;
      this.getCount = 0;
      this.timer = null;
      this.opacityState = 0;
      this.isShowOther = false;
      this.activeId = "";
      this.renderListArr = [];
      this.liftingState = "升序";
      this.isShowCatalog = false;
      this.Did = id;
      this.getDetailData();
      this.DeliverDetailData(obj);
    },
    selectCartoonEvent(data) {
      this.activeId = data.id;
    },
    resetRankEvent() {
      if (this.liftingState == "升序") {
        this.renderListArr.sort(function (a, b) {
          return b.ord - a.ord;
        });
        this.liftingState = "降序";
      } else {
        this.renderListArr.sort(function (a, b) {
          return a.ord - b.ord;
        });
        this.liftingState = "升序";
      }
    },
    closeCurrentList() {
      this.isShowCatalog = !this.isShowCatalog;
    },
    DeliverDetailData(data) {
      let item = {
        ...data,
        season_id: data.id,
        bottom_info: data.styles[0]?.name,
        Did: +new Date(),
      };

      if (localStorage.user) {
        let currStorage = JSON.parse(localStorage.user);
        if (currStorage[this.telephoneString]) {
          if (currStorage[this.telephoneString].isRun) {
            let time = this.dealTimeData();
            if (localStorage.historyRecord) {
              let historyStr = JSON.parse(localStorage.historyRecord);
              if (historyStr[this.telephoneString]) {
                let t = `${time.year}${time.month}${time.day}`;
                let find = historyStr[this.telephoneString].find((it) => {
                  let b = `${it.time.year}${it.time.month}${it.time.day}`;
                  return it.data.season_id == item.season_id && t == b;
                });

                if (!find) {
                  let obj = {};
                  obj[this.telephoneString] = [
                    ...historyStr[this.telephoneString],
                    { data: item, time },
                  ];
                  localStorage.historyRecord = JSON.stringify({
                    ...historyStr,
                    ...obj,
                  });
                }
              } else {
                let obj = {};
                obj[this.telephoneString] = [{ data: item, time }];
                localStorage.historyRecord = JSON.stringify({
                  ...historyStr,
                  ...obj,
                });
              }
            } else {
              let obj = {};
              obj[this.telephoneString] = [{ data: item, time }];
              localStorage.historyRecord = JSON.stringify(obj);
            }
          } else {
            this.$router.push({
              path: "/login",
            });
          }
        } else {
          this.$router.push({
            path: "/login",
          });
        }
      } else {
        this.$router.push({
          path: "/login",
        });
      }
    },
    dealTimeData() {
      let date = new Date();
      let year = date.getFullYear();
      let month = date.getMonth() + 1;
      let day = date.getDate();
      let hour = date.getHours();
      let minute = date.getMinutes();
      let second = date.getSeconds();
      hour = hour < 10 ? "0" + hour : hour;
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;

      return {
        year,
        month,
        day,
        hour,
        minute,
        second,
      };
    },
    ChaseStateEvent() {
      if (localStorage.chaseDataRecord) {
        let currentStr = JSON.parse(localStorage.chaseDataRecord);
        if (currentStr[this.telephoneString]) {
          let arr = currentStr[this.telephoneString];
          let fd = arr.find((item) => item.id == this.Did);
          if (fd) {
            this.isShowChase = false;
            currentStr[this.telephoneString] = arr.filter((item) => {
              return item.id != this.Did;
            });
            localStorage.chaseDataRecord = JSON.stringify(currentStr);
          } else {
            this.isShowChase = true;
            currentStr[this.telephoneString].push(this.detailDataObj);
            localStorage.chaseDataRecord = JSON.stringify(currentStr);
          }
        } else {
          this.isShowChase = true;
          let obj = {};
          obj[this.telephoneString] = [this.detailDataObj];
          localStorage.chaseDataRecord = JSON.stringify({
            ...currentStr,
            ...obj,
          });
        }
      } else {
        this.isShowChase = true;
        let obj = {};
        obj[this.telephoneString] = [this.detailDataObj];
        localStorage.chaseDataRecord = JSON.stringify(obj);
      }
    },
    initChaseStorage() {
      if (localStorage.chaseDataRecord) {
        let currSt = JSON.parse(localStorage.chaseDataRecord);
        if (currSt[this.telephoneString]) {
          let arr = currSt[this.telephoneString];
          let fd = arr.find((item) => item.id == this.Did);
          if (fd) {
            this.isShowChase = true;
          } else {
            this.isShowChase = false;
          }
        } else {
          this.isShowChase = false;
        }
      } else {
        this.isShowChase = false;
      }
    },
    async getImageIndexEvent(id, title) {
      this.isShowArticle = true;
      this.ArticleTitle = title;
      this.closeArticlePage();
      let Arr = await detailGetImageIndex(id);
      this.getImageToken(JSON.stringify(Arr));
    },
    async getImageToken(arr) {
      let Temper = await detailGetImageToken(arr);
      let TokenMessage = Temper.map((item) => {
        return `${item.url}?token=${item.token}`;
      });
      this.articleData = [...TokenMessage];
      this.isShowArticle = false;
    },
    closeArticlePage() {
      this.isCloseArticle = !this.isCloseArticle;
    },
  },
  computed: {
    ...mapState(["telephoneString"]),
  },
  components: {
    ArticlePreviewView,
  },
};
</script>