<template>
  <div class="article-preview">
    <div class="article-header">
      <div class="header-left" @click="articleHidePage">
        <span class="iconfont icon-arrowleft"></span>
      </div>
      <div class="header-title">{{ ArticleTitle }}</div>
    </div>
    <div class="article-body" v-if="!isShowArticle">
      <van-swipe
        class="body-swipe"
        :show-indicators="false"
        :loop="false"
        @change="onChange"
      >
        <van-swipe-item v-for="(item, index) in articleData" :key="index">
          <!-- <div class="img-item" :style="{ backgroundImage: `url(${item})` }"></div> -->
          <van-image :src="item" class="img-item"></van-image>
        </van-swipe-item>
      </van-swipe>
      <div class="article-body-index">
        {{ active }}/{{ articleData.length || 1 }}
      </div>
    </div>
    <div class="loading-body" v-if="isShowArticle">
      <van-loading type="spinner" color="#1989fa" />
    </div>
  </div>
</template>
<style lang="scss" scoped>
.article-preview {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  z-index: 888;
  background: black;

  .article-header {
    position: fixed;
    display: flex;
    align-items: center;
    height: 50px;
    top: 0px;
    left: 0px;
    right: 0px;

    .header-left {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 50px;
      .icon-arrowleft {
        font-size: 20px;
      }
    }
    .header-title {
      width: calc(100% - 60px);
      font-size: 15px;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
  .article-body {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    padding-top: 70px;
    .body-swipe {
      box-sizing: border-box;
      width: 100%;
      height: 86%;
      // background:red;
    }
    .img-item {
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      // background-size: cover;
      // background-position: center center;
      // background-repeat: no-repeat;
    }
    .article-body-index {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 50px;
      color: gray;
      font-size: 14px;
    }
  }
  .loading-body {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
  }
}
</style>
<script>
export default {
  props: ["articleData", "isShowArticle", "ArticleTitle"],
  data() {
    return {
      active: 1,
    };
  },
  methods: {
    onChange(index) {
      this.active = index + 1;
    },
    articleHidePage() {
      this.$emit("closeArticlePage");
    },
  },
};
</script>